  width: 0px;
  opacity: 0.75;
  background-color: rgb(255, 255, 255);
  box-shadow: #fcfcfc 0px 0px 20px 2px;
  opacity: 0;
  top: 100vh;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin: auto;
  z-index: 10;
}

