.containSipin {
  display: flex;
  justify-content: center;
}
.spinning-text-wrapper {
  position: relative;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  display: grid;
  place-items: center;
  font-size: 1.7rem;
  font-weight: bold;
  background-color: black;
  z-index: 20;
  margin-top: 20px;
}

.spinning-text {
  position: absolute;
  width: 300px;
  height: 300px;
  margin: 0;
  animation: spin 50s linear infinite;
  color: white;
}

.spinning-text span {
  position: absolute;
  top: 0;
  left: 50%;
  transform-origin: 0 150px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.spinning-text-wrapper img {
  width: 75%;
  height: auto;
  filter: drop-shadow(10px 10px 5px hsla(0, 0%, 0%, 0.461));
}
@media all and (min-width: 768px) {
  .spinning-text-wrapper img {
    width: 80%;
  }
  .spinning-text {
    width: 400px;
    height: 400px;
  }
  .spinning-text-wrapper {
    width: 400px;
    height: 400px;
    font-size: 2rem;
    margin-top: 50px;
  }
  .spinning-text span {
    transform-origin: 0 200px;
  }
}
@media all and (min-width: 768px) {
  .spinning-text-wrapper img {
    width: 90%;
  }
  .spinning-text {
    width: 600px;
    height: 600px;
  }
  .spinning-text-wrapper {
    width: 600px;
    height: 600px;
    font-size: 2.5rem;
    margin-top: 50px;
  }
  .spinning-text span {
    transform-origin: 0 300px;
  }
}
